.hero-container {
	height: 700px;
	width: 100%;
	position: relative;
}

.hero-content {
	position: absolute;
	top: 172px;
	left: 135px;
}

.hero-container h2 {
	font-size: 3.5rem;
	text-transform: uppercase;
	font-family: 'Articulat CF', 'sans-serif';
}
.hero-container .hero-content button {
	margin-top: 60px;
	font-size: 1.2rem;
	outline: 0;
	background-color: #690ec9;
	color: white;
	border: 0;
	padding: 15px 60px;
	border-radius: 5px;
}

.hero-image {
	position: absolute;
	top: 0;
	right: 0;
	height: 700px;
}

.mobile {
	display: none;
}

@media only screen and (max-width: 600px) {
	.mobile {
		display: flex;
	}
	.wide {
		display: none;
	}
	.hero-container {
		height: 500px;
		width: 100%;
		position: relative;
	}

	.hero-content {
		position: relative;
		z-index: 99;
		top: 25%;
		left: 5%;
	}
	.hero-container h2 {
		font-size: 1.5rem;
		color: #333;
	}
	.hero-container .hero-content button {
		margin-top: 10px;
		font-size: 0.7rem;
		outline: 0;
		border: 0;
		padding: 8px 30px;
		border-radius: 5px;

		font-weight: bold;
	}
	.hero-image.mobile {
		width: 50%;
		height: 100%;
		position: absolute;
		top: 0px;
		z-index: -1;
	}
}
